import React from "react";

export default function MovieList({ movie, view }) {
  return (
    <div style={{ display: "block" }}>
      {movie &&
        movie.map((item, index) => {
          return (
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                marginBottom: "10px",
                cursor: "pointer",
              }}
              onClick={() => view(item)}
              key={index}
            >
              <img
                src={`https://image.tmdb.org/t/p/w500${item.poster_path}`}
                style={{ margin: "auto" }}
                alt={item.title}
                width="20%"
                height="20%"
              />
              <div
                style={{ flexWrap: "wrap", padding: "5px", marginLeft: "10px" }}
              >
                <h4>{item.title || item.name}</h4>
                <p className="discription">
                  {item.overview.substring(0, 150) + " ..."}
                </p>
              </div>
            </div>
          );
        })}
    </div>
  );
}
