export default function MovieView({ view }) {
  return (
    <div style={{ display: "flex", flexDirection: "row" ,flexWrap: "wrap",color:"black"}}>
      {view && (
        <>
          <div style={{ flex:1,margin:'auto'}}>
            <img
              src={`https://image.tmdb.org/t/p/w500${view.poster_path}`}
              alt={view.title}
              width="85%"
             
            />
          </div>
          <div style={{flex:1,padding:"5px"}}>
            <h2>{view.title || view.name}</h2>
            <h4>Release Date :- {view.release_date}</h4>
            <h4>Popularity :- {view.popularity}</h4>
            <h4>Vote Count :- {view.vote_count}</h4>
            <h4>Overview</h4>
            <p>{view.overview}</p>
           
          </div>
          
        </>
      )}
    </div>
  );
}
